import Vue from 'vue';
import Vuex from 'vuex';

import Test from '../javascript/test'

var defaultSettings = {
  questions: 10, //Max number of questions
  biggestNumber: 10, //Largest factor for multiplications
  durationInSeconds: 60, //Seconds
  hints: 3,
  reTake: true,
};

Vue.use(Vuex);
/* ================================= */
//console.log(test.t({lastFor: 2, questions: 10, biggestNumber: 10}).preguntas);

export default new Vuex.Store({
  state: {
    score: 0,
    questionCounter: 0,
    test: Test.begin(defaultSettings),
    currentQuestion: {},
    currentRank: {},
    settings: defaultSettings,
    testStarted: false,
    testEnded: false,
    ranks:[
      {rank: 'Second Lieutenant', img: '1-second lieutenant.jpg', points: 1},
      {rank: 'First Lieutenant', img: '2-first lieutenant.jpg', points: 10},
      {rank: 'Captain', img: '3-captain.jpg', points: 20},
      {rank: 'Major', img: '4-major.jpg', points: 30},
      {rank: 'Lieutenant Colonel', img: '5-lieutenant colonel.jpg', points: 40},
      {rank: 'Colonel', img: '6-colonel.jpg', points: 50},
      {rank: 'Brigadier General', img: '7-brigadier general.jpg', points: 60},
      {rank: 'Major General', img: '8-major general.jpg', points: 70},
      {rank: 'Lieutenant General', img: '9-lieutenant general.jpg', points: 80},
      {rank: 'General', img: '10-general.jpg', points: 90},
      {rank: 'General of the Force', img: '11-general of the force.jpg', points: 95}
    ]
  },
  getters: {
    currentQuestion (state) {
      return state.currentQuestion;
    },
    settings(state){
      return state.settings;
    },
    testEnded(){
      return state.testEnded;
    },
  },  
  mutations: {
    testUpdate(state, opt){
      state.test = null;
      state.test = new Test.begin(opt);
    },
    setCurrentQuestion (state) {
      console.log('setCurrentQuestion');
      //console.log('clock:' + state.test.clock + ', isRunning: ' + state.test.isRunning);
      //Determine current rank
      var newRankAvailable = state.ranks.find(rank => rank.points === state.score);
      if (newRankAvailable !== undefined)
        state.currentRank = newRankAvailable;
      //Then, check if we still have questions to ask
      if ((state.questionCounter < (state.test.questions.length)) 
          && (state.test.isRunning)){
        state.currentQuestion = state.test.questions[state.questionCounter];
        state.questionCounter++;
      // if there is no more questions to ask, the test should stop
      } else {
        console.log('else');
        state.questionCounter++;
        state.testEnded = true;
        state.testStarted = false;
        state.test.ended(); // stops the timer
        // teste ended logic here
      }
    },

    updateAnswer(state, answer){
      console.log('answer:' + answer + 'question: ' + state.currentQuestion.question);
      var question = state.currentQuestion.question.replace('x','*');
      state.currentQuestion.userAnswer = answer;

      if (eval(question) == answer){
        state.score++ // if it is the right answer inrease score by 1;
        state.currentQuestion.isRight = true;
      }
      state.currentQuestion.elapsedTime = state.test.clock;
    },
    init(state){
      if (!state.settings.reTake) state.test.generate();
      state.questionCounter = 0;
      state.score = 0;
      state.testStarted = false;
      state.testEnded = false;
      state.currentQuestion = {};
      state.currentRank = {};
    },

    testStarted(state){
      state.test.start(); // starts the timer\
      state.testStarted = !state.testStarted;
      state.testEnded = false;      
    },

    testEnded(state){
      console.log('from mutations test ended');
      //context.state.testEnded = false;
      var previousTime = 0
      for (var question of state.test.questions) {
        question.clock = question.elapsedTime;
        console.info('before: ', question.elapsedTime);
        question.elapsedTime -= previousTime;
        question.elapsedTime = question.elapsedTime / 1000 + 's';
        previousTime = question.clock;
        console.info('after: ', question.elapsedTime);
      }    
    }

  },

  actions: {
    testStarted(context){
      console.log('testStarted');
      context.commit('init');
      context.commit('testStarted');
      context.commit('setCurrentQuestion');
    },
    checkAnswer(context, answer){
      console.log('checkAnswer');
      context.commit('updateAnswer', answer);
      context.commit('setCurrentQuestion');
      if (context.state.testEnded){
        console.log('testEnded');
        context.commit('testEnded');
      }
    },
    testEnded(context){
      console.log('testEnded');
      context.state.testEnded = false;
      var previousTime = 0
      for (var question of context.state.test.questions) {
        question.clock = question.elapsedTime;
        console.info('before: ', question.elapsedTime);
        question.elapsedTime -= previousTime;
        question.elapsedTime = question.elapsedTime / 1000 + 's';
        previousTime = question.clock;
        console.info('after: ', question.elapsedTime);
      }
      //context.state.test.questions // we need to update the elapsed time 
      //context.commit('init'); // this is more like a reset the test
      //context.commit('testUpdate', context.state.settings);
    },
    testUpdate(context, opt){
      context.commit('testUpdate', opt);
    }
  },
  modules: {
  },
});
