<template>
    <v-container class="navbarIcon">
        <v-tooltip bottom>
        <template v-slot:activator="{ on }">
            <v-btn
            fab
            small
            class="mr-2"
            @click="settings"
            color="#03DAC5"
            v-on="on"
            >
            <v-icon @click.stop="showSettings = !showSettings">mdi-wrench</v-icon>
            </v-btn>
        </template>
        <span>Test settings</span>
        </v-tooltip>

    <!-- Settings -->
    <v-dialog v-model="showSettings" max-width="500px">
      <v-card class="pa-4">
        <v-card-title class="headline">Settings</v-card-title>
        <v-card-text>
          <span>Duration ({{settings.durationInSeconds}} seconds)</span>
          <v-slider
            v-model="settings.durationInSeconds"
            min="0"
            max="360"
            prepend-icon="mdi-av-timer"
            thumb-label
          ></v-slider>
          <span>Number of questions ({{settings.questions}})</span>
          <v-slider
            v-model="settings.questions"
            min="0"
            max="100"
            prepend-icon="mdi-clipboard-list-outline"
            thumb-label
          ></v-slider>
          <span>Largest number ({{settings.biggestNumber}})</span>
          <v-slider
            v-model="settings.largest"
            min="1"
            max="10"
            prepend-icon="mdi-telescope"
            thumb-label
          ></v-slider>
          <v-switch class="mt-n1" v-model="settings.reTake" label="Allow Re-Take?">
          </v-switch>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="closeSettings">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>        

    </v-container>
</template>

<script>
export default {
    name: 'SettingsIcon',
    data: () => {
        return {
            showSettings: false,
        }
    },
    computed: {
        settings() {
          //console.log('settings: ' + this.$store.state.settings);
            return this.$store.state.settings;
        },
    },
    methods:{
        closeSettings(){
            this.settings.hints = 3;
            this.$store.dispatch('testUpdate', this.settings);
            this.showSettings = !this.showSettings;
        }
    }
}
</script>

<style scoped>
  .navbarIcon{
    max-width: 40px;
    display: inline;
  }
</style>
