<template>
    <v-container class="navbarIcon">
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            fab
            small
            class="mr-2"
            @click="stats"
            color="#03DAC5"
            v-on="on"
          >
          <v-icon @click.stop="showStats= true">mdi-chart-bar</v-icon>
          </v-btn>
        </template>
        <span>Test statistics</span>
      </v-tooltip>

      <!-- Stats -->
      <v-dialog v-model="showStats" fullscreen hide-overlay transition="dialog-bottom-transition">
        <v-card>
          <v-toolbar dark color="primary">
            <v-btn icon dark @click="showStats=false">
              <v-icon>mdi-close</v-icon>
            </v-btn>
            <v-toolbar-title>Test analysis</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items>
              <v-btn color="primary" @click="showStats=false">close</v-btn>
            </v-toolbar-items>
          </v-toolbar>

          <v-card class="pa-4">
            <v-card-title class="headline">
              Your score is:
              <span class="pl-5">{{testGrade}}%</span>
            </v-card-title>
            <v-card-text>
              <div class="subtitle-1">Right answers: {{testScore}}</div>

              <div class="subtitle-1">Wrong answers {{wrongAnswers}}</div>
            </v-card-text>
            <v-divider></v-divider>

            <v-data-table
              :headers="headers"
              :items="questions"
              :items-per-page="5"
              class="elevation-1"
            >
            <template v-slot:item.isRight="{ item }">
              <v-simple-checkbox color="primary" v-model="item.isRight"></v-simple-checkbox>
            </template>
            </v-data-table>

          </v-card>

        </v-card>
      </v-dialog>

    </v-container>
</template>
<script>
export default {
    name: 'StatisticsIcon',
    data: () => {
        return {
          showStats: false,
          headers: [
            {text: 'Question', value: 'question'},
            {text: 'Your Answer', value: 'userAnswer'},
            {text: 'Correct?', value: 'isRight'},
            {text: 'Elapsed time', value: 'elapsedTime'}
          ],
        }
    },
    computed:{
      questions(){
        return this.$store.state.test.questions;
      },
      totalQuestions() {
        return this.$store.state.settings.questions;
      },
      testScore() {
        return this.$store.state.score;
      },    
      testGrade(){
        if (this.testScore == 0) return 0;
        var percent = this.testScore / this.totalQuestions;
        return Math.floor(percent * 100);
      },
      wrongAnswers() {
        return this.totalQuestions - this.testScore;
      }
    },
    methods:{
        stats: function(){
          this.showStats = true;
        },
    }
}
</script>

<style scoped>
  .navbarIcon{
    max-width: "50px";
    display: inline;
  }
</style>