<template>
    <v-badge
    color="#03DAC6"
    :content="score"
    icon="mdi-vuetify"
    v-if="score > 0"
    class="mt-6"
    >
        <v-avatar size="28px" class="pr-1">   
            <v-img :src="require('../assets/ranks/' + currentRank.img)"></v-img>
        </v-avatar>{{currentRank.rank}}

    </v-badge>
</template>

<script>
    export default {
        name: 'Ranks', 
        computed: {
            score () {
                return this.$store.state.score;
            },
            currentRank(){
                return this.$store.state.currentRank;
            }
        }
    }
</script>