/* eslint-disable linebreak-style */
class Timer {
  // attributes
  offset;

  clock;

  interval;

  options;

  delay = 100; // how often the timer will be updated (precision of the timmer)

  durationMS = 180000; // number of minutes the timmer should run for in miliseconds
  
  isRunning = false;

  constructor(opt) {
    // default options
    this.options = opt;
    // initialize
    this._reset();
  }

  start() {
  	this.isRunning = true;
    if (!this.interval) {
      this.offset = Date.now();
      this.interval = window.setInterval(this._update.bind(this), this.delay);
    }
  }

  stop() {
    if (this.interval) {
      clearInterval(this.interval);
      this.interval = null;
      this.isRunning =  false;
    }
  }
  
  _reset() {
    this.clock = 0;
    this.isRunning = false;
    if (this.options.durationInSeconds != undefined) 
      this.durationMS = this.options.durationInSeconds * 1000;
    //console.log('this.options.durationInSeconds: ' + this.options.durationInSeconds + ', this.durationMS: ' + this.durationMS);
  }

  _update() {
  	//console.log('update clock:' + this.clock + ", this.durationSeconds:" + this.durationMS);
    this.clock += this._delta();
    if (this.clock >= this.durationMS) this.stop();
  }

  _delta() {
    const now = Date.now();
    const d = now - this.offset;

    this.offset = now;
    return d;
  }
}

