class Test extends Timer {
  questions;

  constructor(opt) {
    super(opt);
    this.options = opt;
    this.generate();
  }

  _randomNumber(max) {
    return Math.floor(Math.random() * (max + 1));
  }

  _shuffle(array) {
    let currentIndex = array.length;
    let temporaryValue;
    let randomIndex;

    // While there remain elements to shuffle...
    while (currentIndex !== 0) {
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;

      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }

    return array;
  }

  ended(){
    this.stop();
  }

  generate() {
    const questions = [];
    const counter = 0;

    for (let i = 0; i < this.options.questions; i++) {
      const x = this._randomNumber(this.options.biggestNumber);
      const y = this._randomNumber(this.options.biggestNumber);
      let hints = [];

      const correctAnswer = x * y;
      hints.push(correctAnswer);

      for (let j = 0; j <= this.options.hints; j++) {
        hints.push(x + this._randomNumber(this.options.biggestNumber) * y);
      }

      hints = hints.reduce(
        (unique, item) => (unique.includes(item) ? unique : [...unique, item]),
        []
      );

      if (hints.length < this.options.hints) {
        hints.push(x);
        hints.push(y);
      }

      hints = hints.slice(0, this.options.hints);

      const question = {
        question: `${x}x${y}`,
        correctAnswer,
        hints: this._shuffle(hints),
        userAnswer: "",
        isRight: false,
        elapsedTime: "",
        progress: (i / this.options.questions ) * 100
      };

      questions.push(question);
    }

    //console.log('Questions:');
    //console.dir(questions);
    this.questions = questions;

    return this.questions;
  }
}

